  font-size: 16px;\n    }\n  }\n\n  &__description {\n    margin: 0 0 12px;\n    color: #888;\n\n    @media (max-width: $mobile-width) {\n      font-size: 12px;\n    }\n  }\n\n  &__price {\n    font-weight: 500;\n    font-size: 24px;\n    line-height: 29px;\n    margin: 0;\n\n    @media (max-width: $mobile-width) {\n      font-size: 16px;\n    }\n  }\n\n  &__actions {\n    display: none;\n    gap: 30px;\n  }\n\n  &__counter {\n    display: none;\n  }\n\n  &__button--favorite,\n  &__button--delete {\n    text-decoration: underline;\n\n    &:hover {\n      color: $hover-color;\n    }\n  }\n\n  &--cart {\n    width: 100%;\n    display: flex;\n    margin-bottom: 0;\n\n    @media (max-width: $mobile-width) {\n      padding: 0 16px;\n    }\n\n    &:hover .interior-item__menu {\n      display: none;\n    }\n\n    .interior-item__image {\n      width: 173px;\n      height: 173px;\n      margin: 0 31px 0 0;\n\n      @media (max-width: $mobile-width) {\n        display: none;\n      }\n    }\n\n    .interior-item__image img {\n      width: 173px;\n      height: 173px;\n      margin-bottom: 0;\n    }\n\n    .interior-item__info {\n      width: 276px;\n    }\n\n    .interior-item__title {\n      margin-bottom: 14px;\n\n      @media (max-width: $mobile-width) {\n        font-size: 18px;\n        line-height: 21px;\n      }\n    }\n\n    .interior-item__description {\n      margin-bottom: 16px;\n    }\n\n    .interior-item__price {\n      font-size: 16px;\n      line-height: 20px;\n      margin-bottom: 15px;\n\n      @media (max-width: $mobile-width) {\n        font-size: 12px;\n        line-height: 16px;\n      }\n    }\n\n    .interior-item__actions {\n      display: flex;\n      gap: 30px;\n    }\n\n    .interior-item__counter {\n      display: flex;\n      width: 71px;\n      height: 51px;\n      margin-left: auto;\n      border-radius: 6px;\n      background-color: #f7f6f6;\n\n      input {\n        width: 71px;\n        height: 51px;\n        border-radius: 6px;\n        background-color: #f7f6f6;\n        text-align: center;\n        border: none;\n\n        &:hover {\n          color: $hover-color;\n        }\n      }\n    }\n  }\n}\n",".cart-items {\n  width: 786px;\n\n  @media (max-width: $tablet-width) {\n    margin-bottom: 64px;\n  }\n\n  @media (max-width: $mobile-width) {\n    width: 100%;\n    margin-bottom: 48px;\n  }\n\n  &__item {\n    padding: 32px 0;\n    border-bottom: 1px solid #cacdd8;\n\n    &:last-child {\n      border-bottom: none;\n    }\n  }\n\n  &__titles {\n    display: flex;\n    justify-content: space-between;\n    padding-bottom: 14px;\n    border-bottom: 1px solid #cacdd8;\n\n    @media (max-width: $mobile-width) {\n      display: none;\n    }\n  }\n\n  &__text {\n    margin: 0;\n    font-size: 16px;\n    line-height: 20px;\n  }\n\n  &__list {\n    list-style: none;\n    margin: 0 0 28px;\n    padding: 0;\n  }\n}\n",".cart-menu {\n  @media (max-width: $mobile-width) {\n    display: flex;\n    justify-content: space-around;\n  }\n\n  &__button {\n    width: 200px;\n    height: 37px;\n    border: 1px solid;\n    border-radius: 50px;\n    padding: 10px 20px;\n    text-align: center;\n    cursor: pointer;\n\n    @media (max-width: $mobile-width) {\n      width: auto;\n    }\n\n    &--clear {\n      border-color: #000;\n      background-color: transparent;\n\n      &:hover {\n        border-color: $hover-color;\n        color: $hover-color;\n      }\n    }\n\n    &--continue {\n      color: #fff;\n      border-color: #fff;\n      background-color: #000;\n\n      &:hover {\n        border-color: $hover-color;\n        color: $hover-color;\n      }\n    }\n  }\n}\n",".cart-order {\n  background-color: #f7f6f6;\n  border-radius: 20px;\n\n  &__container {\n    width: 379px;\n    padding: 42px 28px;\n\n    @media (max-width: $mobile-width) {\n      width: 343px;\n    }\n  }\n\n  &__title {\n    margin: 0 0 35px;\n    font-size: 24px;\n    line-height: 36px;\n    text-align: center;\n  }\n\n  &__total {\n    margin: 0 0 18px;\n    font-weight: 500;\n    font-size: 24px;\n    line-height: 30px;\n    text-align: center;\n  }\n\n  &__buy {\n    width: 288px;\n    padding: 11px;\n    text-align: center;\n    border: 1px solid #040404;\n    border-radius: 20px;\n    font-size: 16px;\n    line-height: 20px;\n    background-color: transparent;\n\n    &:hover {\n      border-color: $hover-color;\n      color: $hover-color;\n    }\n  }\n}\n",".cart-order-form {\n  display: flex;\n  flex-direction: column;\n\n  &__input {\n    width: 288px;\n    border: none;\n    border-bottom: 1px solid #cacdd8;\n    padding-bottom: 3px;\n    background-color: inherit;\n    color: #000;\n    margin-bottom: 44px;\n\n    &:hover {\n      color: $hover-color;\n\n      &::placeholder {\n        color: $hover-color;\n      }\n    }\n\n    &::placeholder {\n      color: #000;\n    }\n\n    &--address {\n      margin-bottom: 68px;\n    }\n  }\n}\n"],"sourceRoot":""}]);
